page design*/\r\n\r\n\r\n    .choose_logo_main_container{\r\n        background-color: #6656e0;\r\n        border-radius:1rem;\r\n    }\r\n\r\n    .logo_head{\r\n\r\n        text-align: center;\r\n        font-family: \"caveat Brush\";\r\n        color:whitesmoke;\r\n        font-size:2em;\r\n        letter-spacing:0.1em;\r\n        padding:1em;\r\n    }\r\n\r\n    .icon_container{\r\n        display: flex;\r\n        justify-content: center;\r\n        margin:0.5rem;\r\n    }\r\n\r\n    .icon_img{\r\n        width: 10%;\r\n        height: 10%;\r\n        background-color: whitesmoke;\r\n        margin:.5rem;\r\n        padding:1rem;\r\n        border-radius: 50%;\r\n        transition:0.3s ease-in-out;\r\n        box-shadow:0px 4px 8px rgba(0,0,0,.5);\r\n    }\r\n\r\n    .icon_img.active{\r\n        background-color:blueviolet;\r\n    }\r\n\r\n\r\n\r\n    /*animation for icon*/\r\n\r\n    .icon_img:hover{\r\n        width:12%;\r\n         height: 12%;\r\n    }\r\n\r\n    .btn_container{\r\n        display: flex;\r\n        justify-content: center;\r\n    }\r\n\r\n    \r\n    .continue_btn{\r\n        background-color: #5bcefa;\r\n        font-family: \"caveat Brush\";\r\n        border:none;\r\n        border-radius: 1em;\r\n        padding:0.5em 1em;\r\n        color:whitesmoke;\r\n        font-size:1.2rem;\r\n        box-shadow:0px 4px 8px rgba(0,0,0,.2);\r\n        margin:1rem;\r\n        cursor: pointer;\r\n\r\n    }\r\n\r\n    .continue_btn:hover{\r\n        \r\n        transform:translateY(-5px);\r\n\r\n    }\r\n\r\n\r\n\r\n\r\n\r\n    /*home  page designs*/\r\n    /*                 */\r\n    /*                */\r\n    /*                */\r\n    /*                */\r\n    /*                */\r\n    /*                */\r\n\r\n\r\n    .welcome_container{\r\n\r\n        background-color: #6656e0;\r\n        border-radius: 2em;\r\n        padding:1.5em;\r\n        \r\n    }\r\n\r\n    .profile_welcome_container{\r\n        display: flex;\r\n        gap:2em;\r\n        color:whitesmoke;\r\n\r\n    }\r\n\r\n    .user_icon{\r\n\r\n        width:10%;\r\n        height:10%;\r\n        background-color: #8470ff;\r\n        border-radius: 50%;\r\n        margin-top:.5em;\r\n    }\r\n\r\n    .msg1{\r\n        font-size:1.5rem;\r\n        letter-spacing: 0.1em;\r\n        font-weight: 200;\r\n    }\r\n\r\n    .msg2{\r\n        font-size:2rem;\r\n        letter-spacing: 0.1em;\r\n    }\r\n\r\n\r\n            /*home page search bar container design*/\r\n\r\n            .main_search_container{\r\n                width:50%;\r\n                height: 3em;\r\n                margin-top:1em;\r\n                background-color: whitesmoke;\r\n                border-radius: 1em;\r\n                padding:.5em;\r\n                \r\n            }\r\n\r\n            .search_container{\r\n                margin:0.5em;\r\n                position: fixed;\r\n                position: relative;\r\n            }\r\n\r\n            .search_input_text{\r\n                border:none;\r\n                background-color: whitesmoke;\r\n                margin-left:.5em;\r\n                outline:none;\r\n                color:gray;\r\n                width: 50%;\r\n                \r\n                position: absolute;\r\n            }\r\n\r\n            .search_icon{\r\n                color:gray;\r\n            }\r\n\r\n            .search_icon:hover{\r\n\r\n                color:#8470ff;\r\n\r\n            }\r\n\r\n\r\n            /*home page progress bar conatiner*/\r\n            .progress_bar_main_container{\r\n\r\n                    width: 20em;\r\n                    height: 12em;\r\n                    position: relative;\r\n                    top:60%;\r\n                    left:50%;\r\n                    transform: translate(-50%,50%);\r\n       \r\n                \r\n                    \r\n            }\r\n\r\n            .progress_head{\r\n                color:white;\r\n                padding-bottom: 1em;\r\n                padding-left:.2em;\r\n\r\n            }\r\n\r\n            .bar_container{\r\n               \r\n                background-color: white;\r\n                width: 110%;\r\n                height: 80%;\r\n               \r\n                border-radius:1em;\r\n                box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.4);\r\n\r\n                display:flex;\r\n                justify-content: center;\r\n                gap:.5em;\r\n                padding:1em;\r\n            }\r\n\r\n            .notifier_container{\r\n\r\n                background-color: rgba(0,0,0,.1);\r\n                margin:.3em;\r\n                width:2em;\r\n                padding-top:.3em;\r\n                height:100%;\r\n                display: flex;\r\n                flex-direction: column;\r\n                justify-content: end;\r\n                padding-left:.5em;\r\n                padding-right:.5em;\r\n\r\n                border-radius: .5em;\r\n                \r\n                box-shadow: 0px 4px 8px rgba(0,0,0,.1);\r\n            \r\n            }\r\n\r\n            .graph_container{\r\n                margin-bottom:.3em;\r\n                border-radius: .2em;\r\n                background-color: rgba(0,0,0,.1);\r\n                width: 1em;\r\n                height: 100%;       \r\n                display: flex;\r\n                flex-direction: column;\r\n                justify-content: end;         \r\n            }\r\n\r\n            .user_progress_notifier{\r\n                background-color:#5bcefa;\r\n                width:1em;\r\n                border-radius: .2em;\r\n            \r\n            }\r\n\r\n\r\n            .progress_para{\r\n               padding-top:10px;\r\n            }\r\n\r\n\r\n\r\n            .show_percentage_container{\r\n\r\n               \r\n                background-color:rgba(255,255,255);\r\n                border-radius: .2em;\r\n                text-align: center;\r\n                font-size:1em;\r\n                pos